import React from 'react';

class Tool extends React.Component {

  render() {
    return (
      <button
        type="button"
        key={this.props.id}
        className='tool'
        onClick={(ev) => {
          this.props.onActivate(this.props.name);
          ev.target.classList.add('sel');
        }}
      >
        {this.props.name}
      </button>
    );
  }
}

export default Tool;
