import React from 'react';
import '../../styles/tools.css';
import Tool from './tool.jsx';

class Tools extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tools: [
        { id: 1, name: 'pen' },
        { id: 2, name: 'bucket' },
        { id: 3, name: 'eraser' },
        { id: 4, name: 'rectangle' },
        { id: 5, name: 'line' },
        { id: 6, name: 'border' },
        { id: 7, name: 'circle' },
        { id: 8, name: 'disc' },
        { id: 9, name: 'picker' },
      ],
      selected: 1,
    };
  }

  activatedTool(id) {
    this.setState({
      selected: id,
    });
  }

  activateClass(id) {
    const classActive = 'active';
    if (id === this.state.selected) {
      return classActive;
    }
  }

  render() {
    return (
      <div className="tools">
        <h2>Tools</h2>
        <ul>
          {this.state.tools.map(tool =>
            <li key={tool.id}
              className={this.activateClass(tool.id)}>
              <Tool
              id={tool.id}
              name={tool.name}
              onActivate = {() => {
                this.props.onActivateTool(tool.name);
                this.activatedTool(tool.id);
              }}
              />
            </li>)}
        </ul>
        <div className="colors">
          <input
            className="primary"
            type="color"
            value={this.props.colors.primary}
            onChange={ev => this.props.onPickColor(ev.target.value)}
          />
          <input
            className="secondary"
            type="color"
            value={this.props.colors.secondary}
            onChange={ev => this.props.onPickColor(ev.target.value)}
          />
        </div>
      </div>
    );
  }
}

export default Tools;
